.commentbox li{
    /* background-color: var(--background); */
    background-color: var(--textbackground);
    display: flex;
    color: black;
    width: 90%;
    padding: 2%;
    border-radius: 2vh;
    margin: 5%;
}
.commentbox img{
    width: 100px;
    height: 100px;
    margin: 2%;
    border-radius: 100%;
    background-image: linear-gradient(var(--basecolor),var(--basecolor));
}
.commentbox h1{
        font-family: 'Montserrat', sans-serif;
        font-size: 3vh;
        font-weight: 800;
        padding: 1%;
        align-items: center;
}

.designationspan{
        font-family: 'Montserrat', sans-serif;
        font-size: 2vh;
}
.commentbox ul{
    display:  flex;
    flex-direction: column;
    justify-content: space-around;
}

.timebox{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bold;
    /* color: var(--basecolor); */
    margin-top: 2%;
    display: flex;
}
.time,.date{
    background-color: var(--basecolor);
    padding-inline: 0.5%;
    border-radius: 2vw;
    color: var(--text);
}
.time{
    grid: 3;
}

.date{
    margin-left: auto;
    grid: 1;
}
@media only screen and (min-width:650px) and (max-width:1020) {
    .commentbox img {
            width: 100%;
            height: 30vw;
            margin: 1%;
            border-radius: 100%;
        }

    .container button{
        height: 10vw;
        width: 20vw;
    }
}